import * as React from 'react';
import { RenderToolbar, ItemKey } from '@ali/4ever-component';
import createCommentButton from './components/next/createCommentButton';

export default function createRenderToolbar(
  locale: Record<string, string>,
): RenderToolbar {

  return (props, controller) => {

    const { buttonName, extraProps } = props;

    if (buttonName === ItemKey.comment) {
      return createCommentButton({
        controller,
        locale,
        ...extraProps,
      })
    }

    return null
  };
}
